<template>
  <el-alert v-if="show_tips" title="v1.0.5" type="success"></el-alert>
  <el-row class="tool-bar" justify="space-between">
    <el-col :span="20">{{title}}</el-col>
    <el-col :xs="4" :sm="4" :md="4" :lg="4">
      <el-row justify="end">
        <el-col :span="6">
          <!--   -->
          <el-tooltip effect="dark" content="/" placement="bottom">
            <div>
              <svg-icon name="layout" className="icon" @click="handleClickToolBtn(1)" />
            </div>
          </el-tooltip>
        </el-col>
        <el-col :span="6">
          <!--   -->
          <el-tooltip effect="dark" content="" placement="bottom">
            <div>
              <svg-icon name="source_code" className="icon" @click="handleClickToolBtn(2)" />
            </div>
          </el-tooltip>
        </el-col>
        <el-col :span="6">
          <!--   -->
          <el-tooltip effect="dark" content="" placement="bottom">
            <div>
              <svg-icon name="clear_layout" className="icon" @click="handleClickToolBtn(3)" />
            </div>
          </el-tooltip>
        </el-col>
        <el-col :span="6">
          <!--   -->
          <el-tooltip effect="dark" content=" DELETE" placement="bottom">
            <div>
              <svg-icon name="delete" className="icon" @click="handleClickToolBtn(4)" />
            </div>
          </el-tooltip>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>
<script lang="ts">
import { defineComponent, reactive, toRefs, onMounted } from 'vue'
import { useStore } from 'vuex'

export default defineComponent({
  name: 'ToolBar',
  //
  emits: ['handleChangeDesigner', 'handleClearLayout', 'handleDeleteControl'],
  setup(props, { emit }) {
    const store = useStore()

    //
    let data: any = reactive({
      title: '', //
      show_tips: true, //
    })

    //
    onMounted(() => {
      setTimeout(() => {
        data.show_tips = false //
      }, 3000)
    })

    //
    const handleClickToolBtn = (val: number) => {
      switch (val) {
        case 1:
          //
          if (data.title == '') {
            store.dispatch('handleChangeShowBorder', !store.state.show_border)
          }
          data.title = ''
          emit('handleChangeDesigner', val)
          break
        case 2:
          data.title = ''
          emit('handleChangeDesigner', val)
          break
        case 3:
          if (data.title == '') {
            //
            emit('handleClearLayout', val)
          }
          break
        case 4:
          if (data.title == '') {
            //
            emit('handleDeleteControl', val)
          }
          break
        default:
          break
      }
    }

    return {
      ...toRefs(data),
      handleClickToolBtn,
    }
  },
})
</script>
<style lang="scss" scoped>
.tool-bar {
  padding: 10px 20px;
  background: $bg;
  color: $h2c;
}
.icon {
  -webkit-app-region: no-drag;
  width: 20px;
  height: 20px;
  fill: $h3c;
  cursor: pointer;
  outline: none;
  &:hover {
    fill: $h1c;
  }
}
</style>