<template>
  <el-aside class="attribute-bar" width="400px">
    <el-tabs v-model="tab_active_name">
      <el-tab-pane name="1">
        <template #label>
          <div class="icon-box tab-label">
            <svg-icon name="attribute" className="icon" />
          </div>
        </template>
        <h4>{{node_info.title}}</h4>
        <el-form ref="attribute" :model="attribute" label-width="68px">
          <!-- el-container start -->
          <template v-if="node_info.name == 'el-container'">
            <el-form-item v-if="attribute.direction||attribute.direction==''" label="">
              <el-select v-model="attribute.direction" placeholder="" clearable>
                <el-option key="horizontal " label="" value="horizontal "></el-option>
                <el-option key="vertical" label="" value="vertical"></el-option>
              </el-select>
            </el-form-item>
          </template>
          <!-- el-container end -->
          <!-- el-row start -->
          <template v-if="node_info.name == 'el-row'">
            <el-form-item v-if="node_info.text||node_info.text==''" label="">
              <el-input v-model="node_info.text" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.gutter||attribute.gutter==0" label="">
              <el-input-number v-model="attribute.gutter" :controls="true" :min="0">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.justify||attribute.justify==''" label="">
              <el-select v-model="attribute.justify" placeholder="" clearable>
                <el-option key="start" label="" value="start"></el-option>
                <el-option key="center" label="" value="center"></el-option>
                <el-option key="end" label="" value="end"></el-option>
                <el-option key="space-around" label="(around)" value="space-around"></el-option>
                <el-option key="space-between" label="(between)" value="space-between"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="attribute.align||attribute.align==''" label="">
              <el-select v-model="attribute.align" placeholder="" clearable>
                <el-option key="top" label="" value="top"></el-option>
                <el-option key="middle" label="" value="middle"></el-option>
                <el-option key="bottom" label="" value="bottom"></el-option>
              </el-select>
            </el-form-item>
          </template>
          <!-- el-row end -->
          <!-- el-col start -->
          <template v-if="node_info.name == 'el-col'">
            <el-form-item v-if="node_info.text||node_info.text==''" label="">
              <el-input v-model="node_info.text" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.span" label="">
              <el-input-number v-model="attribute.span" :controls="true" :min="1" :max="24">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.offset||attribute.offset==0" label="">
              <el-input-number v-model="attribute.offset" :controls="true" :min="0" :max="24">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.push||attribute.push==0" label="">
              <el-input-number v-model="attribute.push" :controls="true" :min="0" :max="24"
                               :disabled="attribute.pull>0">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.pull||attribute.pull==0" label="">
              <el-input-number v-model="attribute.pull" :controls="true" :min="0" :max="24"
                               :disabled="attribute.push>0">
              </el-input-number>
            </el-form-item>
          </template>
          <!-- el-col end -->
          <!-- el-button start -->
          <template v-if="node_info.name == 'el-button'">
            <el-form-item v-if="node_info.text||node_info.text==''" label="">
              <el-input v-model="node_info.text" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.size||attribute.size==''" label="">
              <el-select v-model="attribute.size" placeholder="" clearable>
                <el-option key="default" label="" value="default"></el-option>
                <el-option key="small" label="" value="small"></el-option>
                <el-option key="large" label="" value="large"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="attribute.type||attribute.type==''" label="">
              <el-select v-model="attribute.type" placeholder="" clearable>
                <el-option key="default" label="" value="default"></el-option>
                <el-option key="primary" label="" value="primary"></el-option>
                <el-option key="success" label="" value="success"></el-option>
                <el-option key="warning" label="" value="warning"></el-option>
                <el-option key="danger" label="" value="danger"></el-option>
                <el-option key="info" label="" value="info"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.text" @change="attribute.text==false?attribute.bg=false:''"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.text" label="">
              <el-switch v-model="attribute.bg"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.plain"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.round"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.circle"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.loading"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.icon||attribute.icon==''" label="">
              <el-input v-model="attribute.icon" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.autofocus"></el-switch>
            </el-form-item>
          </template>
          <!-- el-button end -->
          <!-- el-form-item start -->
          <template v-if="node_info.name == 'el-form'">
            <el-form-item v-if="node_info.model||node_info.model==''" label="">
              <el-input v-model="node_info.model" placeholder=" form" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.ref||attribute.ref==''" label="ref">
              <el-input v-model="attribute.ref" placeholder="ref" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.inline"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute['label-position']||attribute['label-position']==''" label="">
              <el-select v-model="attribute['label-position']" placeholder="" clearable>
                <el-option key="left" label="" value="left"></el-option>
                <el-option key="top" label="" value="top"></el-option>
                <el-option key="right" label="" value="right"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="attribute['label-width']||attribute['label-width']==''" label="">
              <el-input v-model="attribute['label-width']" placeholder=" 100px" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.size||attribute.size==''" label="">
              <el-select v-model="attribute.size" placeholder="" clearable>
                <el-option key="default" label="" value="default"></el-option>
                <el-option key="small" label="" value="small"></el-option>
                <el-option key="large" label="" value="large"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
          </template>
          <!-- el-form-item end -->
          <!-- el-form-item start -->
          <template v-if="node_info.name == 'el-form-item'">
            <el-form-item v-if="attribute.prop||attribute.prop==''" label="">
              <el-input v-model="attribute.prop" placeholder=" prop" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.label||attribute.label==''" label="">
              <el-input v-model="attribute.label" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.required"></el-switch>
            </el-form-item>
          </template>
          <!-- el-form-item end -->
          <!-- el-radio-group start -->
          <template v-if="node_info.name == 'el-radio-group'">
            <!--  -->
            <el-form-item v-if="attribute['v-model']||attribute['v-model'] == ''" label="">
              <el-input v-model="attribute['v-model']" placeholder=" form.prop" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <template v-for="(item,index) in node_info.children" :key="index">
                <el-row>
                  <el-col :span="10">
                    <el-input size="small" v-model="item.text" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="8" :offset="1">
                    <el-input size="small" v-model="item.props.label" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="4" :offset="1">
                    <el-button v-if="index > 1" type="danger" @click="handleClickDeleteRadioItem(node_info,index)">
                      <svg-icon name="minus" className="icon" />
                    </el-button>
                  </el-col>
                </el-row>
              </template>
              <el-row>
                <el-col :span="4" :offset="1">
                  <el-button type="primary" @click="handleClickAddRadioItem(node_info)">
                    <svg-icon name="plus" className="icon" />
                  </el-button>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
          </template>
          <!-- el-radio-group end -->
          <!-- el-checkbox-group start -->
          <template v-if="node_info.name == 'el-checkbox-group'">
            <!--  -->
            <el-form-item v-if="attribute['v-model']||attribute['v-model'] == ''" label="">
              <el-input v-model="attribute['v-model']" placeholder=" form.prop" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <template v-for="(item,index) in node_info.children" :key="index">
                <el-row>
                  <el-col :span="10">
                    <el-input size="small" v-model="item.text" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="8" :offset="1">
                    <el-input size="small" v-model="item.props.label" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="4" :offset="1">
                    <el-button v-if="index > 1" type="danger" @click="handleClickDeleteCheckBoxItem(node_info,index)">
                      <svg-icon name="minus" className="icon" />
                    </el-button>
                  </el-col>
                </el-row>
              </template>
              <el-row>
                <el-col :span="4" :offset="1">
                  <el-button type="primary" @click="handleClickAddCheckBoxItem(node_info)">
                    <svg-icon name="plus" className="icon" />
                  </el-button>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
          </template>
          <!-- el-checkbox-group end -->
          <!-- el-input start -->
          <template v-if="node_info.name == 'el-input'">
            <el-form-item v-if="attribute['v-model']||attribute['v-model'] == ''" label="">
              <el-input v-model="attribute['v-model']" placeholder=" form.prop" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.type||attribute.type==''" label="">
              <el-select v-model="attribute.type" placeholder="" clearable>
                <el-option key="text" label="" value="text"></el-option>
                <el-option key="number" label="" value="number"></el-option>
                <el-option key="password" label="" value="password"></el-option>
                <el-option key="textarea" label="" value="textarea"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="attribute.maxlength||attribute.maxlength==0" label="">
              <el-input-number v-model="attribute.maxlength" :controls="true" :min="0">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.minlength||attribute.minlength==0" label="">
              <el-input-number v-model="attribute.minlength" :controls="true" :min="0">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.placeholder||attribute.placeholder==''" label="">
              <el-input v-model="attribute.placeholder" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.clearable"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute['show-password']"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.type == 'textarea'&&(attribute.rows||attribute.rows==0)" label="">
              <el-input-number v-model="attribute.rows" :controls="true" :min="2">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.type == 'textarea'" label="">
              <el-switch v-model="attribute.autosize"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.type == 'textarea'" label="">
              <el-select v-model="attribute.resize" placeholder="" clearable>
                <el-option key="none" label="" value="none"></el-option>
                <el-option key="both" label="" value="both"></el-option>
                <el-option key="horizontal" label="" value="horizontal"></el-option>
                <el-option key="vertical" label="" value="vertical"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.readonly"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
          </template>
          <!-- el-input end -->
          <!-- el-input-number start -->
          <template v-if="node_info.name == 'el-input-number'">
            <el-form-item v-if="attribute['v-model']||attribute['v-model'] == ''" label="">
              <el-input v-model="attribute['v-model']" placeholder=" form.prop" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.max||attribute.max==0" label="">
              <el-input-number v-model="attribute.max" :controls="true" :min="0">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.min||attribute.min==0" label="">
              <el-input-number v-model="attribute.min" :controls="true" :min="0">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.step||attribute.step==0" label="">
              <el-input-number v-model="attribute.step" :controls="true" :min="1">
              </el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute['step-strictly']"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.precision||attribute.precision==0" label="">
              <el-input-number v-model="attribute.precision" :controls="true" :min="0">
              </el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.controls"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.placeholder||attribute.placeholder==''" label="">
              <el-input v-model="attribute.placeholder" placeholder="" clearable></el-input>
            </el-form-item>
          </template>
          <!-- el-input-number end -->
          <!-- el-select start -->
          <template v-if="node_info.name == 'el-select'">
            <!--  -->
            <el-form-item v-if="attribute['v-model']||attribute['v-model'] == ''" label="">
              <el-input v-model="attribute['v-model']" placeholder=" form.prop" clearable></el-input>
            </el-form-item>
            <el-form-item :label-width="0">
              <el-row>
                <label class="el-form-item__label" style="width: 68px;"></label>
              </el-row>
              <template v-for="(item,index) in node_info.children" :key="index">
                <el-row>
                  <el-col :span="7">
                    <el-input size="small" v-model="item.text" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="7" :offset="1">
                    <el-input size="small" v-model="item.props.label" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="4" :offset="1">
                    <el-input size="small" v-model="item.props.value" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="3" :offset="1">
                    <el-button v-if="index > 1" type="danger" @click="handleClickDeleteSelectItem(node_info,index)">
                      <svg-icon name="minus" className="icon" />
                    </el-button>
                  </el-col>
                </el-row>
              </template>
              <el-row>
                <el-col :span="4" :offset="1">
                  <el-button type="primary" @click="handleClickAddSelectItem(node_info)">
                    <svg-icon name="plus" className="icon" />
                  </el-button>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.multiple" @change="((val)=>{
                          if(val){
                            node_info.value = []
                          }else{
                            node_info.value = ''
                            attribute['collapse-tags'] = false
                          }
                         })">
              </el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.clearable"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.multiple" label="">
              <el-switch v-model="attribute['collapse-tags']"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.filterable"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.placeholder||attribute.placeholder==''" label="">
              <el-input v-model="attribute.placeholder" placeholder="" clearable></el-input>
            </el-form-item>
          </template>
          <!-- el-select end -->
          <!-- el-switch start -->
          <template v-if="node_info.name == 'el-switch'">
            <el-form-item v-if="attribute['v-model']||attribute['v-model'] == ''" label="">
              <el-input v-model="attribute['v-model']" placeholder=" form.prop" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
          </template>
          <!-- el-switch end -->
          <!-- el-date-picker start -->
          <template v-if="node_info.name == 'el-date-picker'">
            <el-form-item v-if="attribute['v-model']||attribute['v-model'] == ''" label="">
              <el-input v-model="attribute['v-model']" placeholder=" form.prop" clearable></el-input>
            </el-form-item>
            <el-form-item v-if="attribute.type||attribute.type==''" label="">
              <el-select v-model="attribute.type" placeholder="" @change="node_info.value = ''" clearable>
                <el-option key="year" label="" value="year"></el-option>
                <el-option key="month" label="" value="month"></el-option>
                <el-option key="date" label="" value="date"></el-option>
                <el-option key="dates" label="" value="dates"></el-option>
                <el-option key="datetime" label="" value="datetime"></el-option>
                <el-option key="datetimerange" label="" value="datetimerange"></el-option>
                <el-option key="daterange" label="" value="daterange"></el-option>
                <el-option key="monthrange" label="" value="monthrange"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="attribute.format||attribute.format==''" label="">
              <el-input v-model="attribute.format" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.readonly"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.disabled"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.editable"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="attribute.clearable"></el-switch>
            </el-form-item>
            <template v-if="attribute.type=='year' || attribute.type=='month' || attribute.type=='date' 
                  || attribute.type=='dates'|| attribute.type=='datetime'">
              <el-form-item v-if="attribute.placeholder||attribute.placeholder==''" label="">
                <el-input v-model="attribute.placeholder" placeholder="" clearable></el-input>
              </el-form-item>
            </template>
            <template v-else>
              <el-form-item v-if="attribute['start-placeholder']||attribute['start-placeholder']==''" label="">
                <el-input v-model="attribute['start-placeholder']" placeholder="" clearable></el-input>
              </el-form-item>
              <el-form-item v-if="attribute['end-placeholder']||attribute['end-placeholder']==''" label="">
                <el-input v-model="attribute['end-placeholder']" placeholder="" clearable></el-input>
              </el-form-item>
              <el-form-item v-if="attribute['range-separator']||attribute['range-separator']==''" label="">
                <el-input v-model="attribute['range-separator']" placeholder="" clearable></el-input>
              </el-form-item>
            </template>
          </template>
          <!-- el-date-picker end -->
          <!-- el-el-descriptions start -->
          <template v-if="node_info.name == 'el-descriptions'">
            <!--  -->
            <el-form-item label="">
              <el-switch v-model="attribute.border"></el-switch>
            </el-form-item>
            <el-form-item v-if="attribute.column" label="">
              <!-- 5 -->
              <el-input-number v-model="attribute.column" :controls="true" :min="1" :max="5">
              </el-input-number>
            </el-form-item>
            <el-form-item v-if="attribute.direction||attribute.direction==''" label="">
              <el-select v-model="attribute.direction" placeholder="" clearable>
                <el-option key="horizontal " label="" value="horizontal "></el-option>
                <el-option key="vertical" label="" value="vertical"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="attribute.size||attribute.size==''" label="">
              <el-select v-model="attribute.size" placeholder="" clearable>
                <el-option key="default" label="" value="default"></el-option>
                <el-option key="small" label="" value="small"></el-option>
                <el-option key="large" label="" value="large"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="attribute.title||attribute.title==''" label="">
              <el-input v-model="attribute.title" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item :label-width="0">
              <el-row>
                <label class="el-form-item__label" style="width: 68px;"></label>
              </el-row>
              <template v-for="(item,index) in node_info.children" :key="index">
                <el-row class="el-desc-item-top">
                  <el-col :span="9">
                    <el-input size="small" v-model="item.props.label" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="9" :offset="1">
                    <el-select v-model="item.props['label-align']" placeholder="" clearable>
                      <el-option key="left" label="" value="left"></el-option>
                      <el-option key="center" label="" value="center"></el-option>
                      <el-option key="right" label="" value="right"></el-option>
                    </el-select>
                  </el-col>
                </el-row>
                <el-row class="el-desc-item">
                  <el-col :span="9">
                    <el-input size="small" v-model="item.text" placeholder="" clearable></el-input>
                  </el-col>
                  <el-col :span="9" :offset="1">
                    <el-select v-model="item.props.align" placeholder="" clearable>
                      <el-option key="left" label="" value="left"></el-option>
                      <el-option key="center" label="" value="center"></el-option>
                      <el-option key="right" label="" value="right"></el-option>
                    </el-select>
                  </el-col>
                  <el-col :span="4" :offset="1">
                    <el-button v-if="index > 1" type="danger"
                               @click="handleClickDeleteDescriptionsItem(node_info,index)">
                      <svg-icon name="minus" className="icon" />
                    </el-button>
                  </el-col>
                </el-row>
              </template>
              <el-row>
                <el-col :span="4" :offset="1">
                  <el-button type="primary" @click="handleClickAddDescriptionsItem(node_info)">
                    <svg-icon name="plus" className="icon" />
                  </el-button>
                </el-col>
              </el-row>
            </el-form-item>
          </template>
          <!-- el-descriptions end -->
          <!-- public attribute start-->
          <el-form-item v-if="attribute.class||attribute.class==''" label="class">
            <el-input v-model="attribute.class" placeholder="css" clearable></el-input>
          </el-form-item>
          <el-form-item v-if="node_info.style||node_info.style==''" label="">
            <el-input type="textarea" v-model="node_info.style" placeholder="" autosize clearable></el-input>
          </el-form-item>
          <!-- public attribute end-->
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="" name="2">
        <template #label>
          <div class="icon-box tab-label">
            <svg-icon name="tree" className="icon" />
          </div>
        </template>
        <el-tree ref="treeRef" :data="tree" :props="defaultProps" :render-after-expand="false" :highlight-current="true"
                 node-key="id" :indent="32" :default-expand-all="true" :expand-on-click-node="false"
                 @node-click="handleClickTreeNode" draggable :allow-drop="handleAllowDrop"
                 :filter-node-method="handleFilter">
          <template #="{ node, data }">
            <!--  -->
            <div class="custom-tree-node">
              {{data.name}}
            </div>
            <!--   -->
            <div class="custom-tree-node-block" @mouseover="handleMouseOverTreeNode(node,data)"
                 @mouseout="handleMouseOutTreeNode(node,data)"> </div>
          </template>
        </el-tree>
      </el-tab-pane>
    </el-tabs>

  </el-aside>
</template>
<script lang="ts">
import {
  getCurrentInstance,
  defineComponent,
  reactive,
  toRefs,
  computed,
  watch,
  nextTick,
  ref,
} from 'vue'
import { useStore } from 'vuex'

//
import mixins from '@/mixins/index'

export default defineComponent({
  name: 'AttributeBar',
  setup() {
    const store = useStore()

    //
    const { proxy } = getCurrentInstance() as any

    //ref
    const treeRef: any = ref(null)

    //
    let data: any = reactive({
      tab_active_name: '1', //tab
      tree: store.state.component_tree_list, //
      defaultProps: {
        children: 'children',
        label: 'name',
      }, // 
    })

    const {
      _handleRecursionGetNodeByNodeId, //id
    } = mixins()

    //
    const node_info: any = computed({
      get() {
        return store.state.current_node_info
      },
      set(val) {},
    })

    //
    const attribute: any = computed({
      get() {
        return store.state.current_node_info.props
      },
      set(val) {},
    })

    // 
    watch(
      attribute,
      (a: any, b: any) => {
        //
        nextTick(() => {
          //
          let border = document.getElementsByClassName(
            'border'
          )[0] as HTMLElement
          if (border) {
            border.className = border.className.replace(' border', '')
          }

          //
          let node_element = document.getElementById(
            store.state.current_node_info.id
          ) as HTMLElement
          if (node_element) {
            node_element.className = node_element.className + ' border'
          }
        })
      },
      { deep: true }
    )

    watch(store.state.component_tree_list, (val: any) => {
      nextTick(() => {
        //
        treeRef.value.filter()
      })
    })

    //
    const handleClickAddRadioItem = (node_info: any) => {
      //
      let radio: any = {
        name: 'el-radio',
        title: '',
        props: {
          label: '',
        },
        text: '',
        style: '',
        children: [],
      }

      radio.id = 'node-' + new Date().getTime()

      node_info.children.push(radio)
    }

    //
    const handleClickDeleteRadioItem = (node_info: any, index: any) => {
      node_info.children.splice(index, 1)
    }

    //
    const handleClickAddCheckBoxItem = (node_info: any) => {
      //
      let checkbox: any = {
        name: 'el-checkbox',
        title: '',
        props: {
          label: '',
        },
        text: '',
        style: '',
        children: [],
      }

      checkbox.id = 'node-' + new Date().getTime()

      node_info.children.push(checkbox)
    }

    //
    const handleClickDeleteCheckBoxItem = (node_info: any, index: any) => {
      node_info.children.splice(index, 1)
    }

    //
    const handleClickAddSelectItem = (node_info: any) => {
      //
      let select: any = {
        name: 'el-option',
        title: '',
        props: {
          label: '',
          value: '',
        },
        style: '',
        children: [],
      }

      select.id = 'node-' + new Date().getTime()

      node_info.children.push(select)
    }

    //
    const handleClickDeleteSelectItem = (node_info: any, index: any) => {
      node_info.children.splice(index, 1)
    }

    //
    const handleClickAddDescriptionsItem = (node_info: any) => {
      //
      let descriptions: any = {
        name: 'el-descriptions-item',
        props: {
          label: '',
          span: 1,
          width: '',
          'min-width': '',
          align: '',
          'label-align': '',
        },
        text: '',
        style: '',
        children: [],
      }

      descriptions.id = 'node-' + new Date().getTime()

      node_info.children.push(descriptions)
    }

    //
    const handleClickDeleteDescriptionsItem = (node_info: any, index: any) => {
      node_info.children.splice(index, 1)
    }

    //
    const handleClickTreeNode = (val: any) => {
      //
      if (val.id) {
        let node_info: any = _handleRecursionGetNodeByNodeId(val.id, data.tree)

        //
        store.dispatch('handleChangeCurrentNodeInfo', node_info)

        //
        let border = document.getElementsByClassName(
          'hover-border'
        )[0] as HTMLElement
        if (border) {
          border.className = border.className.replace(' hover-border', '')
        }

        //
        border = document.getElementsByClassName('border')[0] as HTMLElement
        if (border) {
          border.className = border.className.replace(' border', '')
        }

        //
        let node_element = document.getElementById(
          store.state.current_node_info.id
        ) as HTMLElement
        if (node_element) {
          node_element.className = node_element.className + ' border'
        }
      }
    }

    //
    const handleMouseOverTreeNode = (node: any, data: any) => {
      if (data.id) {
        //
        let node_element = document.getElementById(data.id) as HTMLElement
        if (node_element) {
          node_element.className = node_element.className + ' hover-border'
        }
      }
    }
    //
    const handleMouseOutTreeNode = (node: any, data: any) => {
      //
      let border = document.getElementsByClassName(
        'hover-border'
      )[0] as HTMLElement
      if (border) {
        border.className = border.className.replace(' hover-border', '')
      }
    }

    //
    const handleAllowDrop = (draggingNode: any, dropNode: any, type: any) => {
      if (type == 'inner' && !dropNode.data.allow) {
        //
        return false
      } else {
        return true
      }
    }

    //
    const handleFilter = (value: any, data: any) => {
      if (
        data.name == 'el-radio' ||
        data.name == 'el-checkbox' ||
        data.name == 'el-option'
      ) {
        return false
      }
      return true
    }

    return {
      ...toRefs(data),
      treeRef,
      node_info,
      attribute,
      handleClickAddRadioItem,
      handleClickDeleteRadioItem,
      handleClickAddCheckBoxItem,
      handleClickDeleteCheckBoxItem,
      handleClickAddSelectItem,
      handleClickDeleteSelectItem,
      handleClickAddDescriptionsItem,
      handleClickDeleteDescriptionsItem,
      handleClickTreeNode,
      handleMouseOverTreeNode,
      handleMouseOutTreeNode,
      handleAllowDrop,
      handleFilter,
    }
  },
})
</script>
<style lang="scss" scoped>
.attribute-bar {
  padding: 20px;
  background: #ffffff;
  border-left: 1px solid #e6e6e6;
  .icon {
    fill: $white;
  }
  .tab-label {
    .icon {
      fill: $h3c;
    }
  }
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    font-size: 14px;
    height: 100%;
  }
  .custom-tree-node-block {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  .el-desc-item-top {
    padding-top: 6px;
    border-top: 1px solid #ececec;
  }
  .el-desc-item {
    padding-top: 2px;
    padding-bottom: 6px;
  }
}
</style>